import React, { Fragment, useRef, useState } from 'react';

import { Button } from '@material-ui/core';
import data from './data.json';
import { grey } from '@material-ui/core/colors';

// import randomcolor from "randomcolo?r";
const csvData = '';

const Card = (props) => {
	return (
		<ul>
			{props.data.map((item) => (
				<Fragment key={item.name}>
					<li>
						<div className='card'>
							<div className='card-body'>
								{item.name}
								{/* <h4>{faker.name.findName()}</h4>
                <p>{faker.name.jobTitle()}</p> */}
							</div>
							<div></div>
						</div>
						{item.children?.length && <Card data={item.children} />}
					</li>
				</Fragment>
			))}
		</ul>
	);
};

const Chart = () => {
	const [emps, setemps] = useState([]);
	const txt = useRef();
	const updateTxt = () => {
		setemps(txt.current.value);
		console.log(txt.current.value.split('\n'));
		const employees = txt.current.value.split('\n');
		const employeeChart = {};
		const mapping = { name: 1, designation: 2, manager: 3 };
		const h = {};
        employees.map((employee) => {
			const emp = employee.split(',');
            const manager = emp[mapping.manager] ? emp[mapping.manager] : "root";
			if (employeeChart.hasOwnProperty(manager)) {
				employeeChart[manager].children.push(emp[mapping.name]);
			} else {
				employeeChart[manager] = { children: [emp[mapping.name]] };
			}
		});
        // for(let k in employeeChart) {
        //     employeeChart[k].children.map(ec => {
        //         h[k]
        //     })
        // }
        
		console.log(employeeChart);
	};
	console.log();
	return (
		<div className='org-tree'>
			<textarea ref={txt}>
			</textarea>
			<Button onClick={updateTxt}>Update</Button>
			<Card data={data} />
		</div>
	);
};

export default Chart;
